<template>
  <form
    class="w-full max-w-screen-sm mx-auto mt-10"
    @submit.prevent="handleSubmit"
  >
    <FormInput
      placeholder="請輸入會員帳號"
      :error="accountError"
      :value="account"
      @onChange="handleChangeAccount"
    >
      <router-link to="/member/user/forget-account" class="underline">
        忘記帳號
      </router-link>
    </FormInput>
    <FormInput
      placeholder="請輸入密碼（6-20英數字,不含特殊符號）"
      :error="passwordError"
      :value="password"
      @onChange="handleChangePassword"
      type="password"
    >
      <router-link to="/member/user/forget-password" class="underline">
        忘記密碼
      </router-link>
    </FormInput>
    <div class="text-xl flex items-center gap-2.5 mb-9">
      <Checkbox :value="isRemember" @onChange="handleChangeRemember" />
      記住我
    </div>
    <button
      class="rounded w-full py-2.5 lg:py-4 text-center text-white bg-button hover:bg-main-red text-xl lg:text-3xl mt-9"
    >
      登入
    </button>
  </form>
</template>
<script>
import FormInput from "@/components/FormInput";
import Checkbox from "@/components/Checkbox";
import { mapActions } from "vuex";

export default {
  components: {
    FormInput,
    Checkbox,
  },
  data() {
    return {
      isRemember: false,
      account: "",
      accountError: "",
      password: "",
      passwordError: "",
    };
  },
  methods: {
    ...mapActions(["login"]),
    handleChangeRemember(value) {
      this.isRemember = value;
    },
    handleChangeAccount(value) {
      this.accountError = "";
      this.account = value;
    },
    handleChangePassword(value) {
      this.passwordError = "";
      this.password = value;
    },
    handleSubmit() {
      if (!this.account) {
        this.accountError = "*查無此使用者帳號";
        return;
      }
      if (!this.password) {
        this.passwordError = "*請使用6-20英數字,不含特殊符號";
        return;
      }
      this.login(this.isRemember);
      this.$router.push("/");
    },
  },
};
</script>
